import postpreview from "~/components/posts/post-preview.vue"
export default {
    components: {
        postpreview
    },
    async asyncData({ $content, params }) {
        try {
            const posts = await $content("posts").sortBy('date', 'desc').fetch();
            return { posts };
        } catch {
            (err) => {
                console.log(err);
            };
        }
    }
};
